<template>
    <div id="downloadList">
        <section :key="index"
                 v-for="(item, index) in downloadList">
            <DownloadItem :class="index === downloadList.length - 1 ? 'border-bottom' : ''"
                          :item="item"
                          class="border-top"/>
        </section>

        <RightBottom/>
    </div>
</template>

<script>

    import DownloadItem from "@/components/DownloadItem";
    import RightBottom from "@/components/RightBottom";
    import {mapState} from "vuex";

    export default {
        name: "DownloadList",
        created() {
            // 根据路由的参数来显示不同的列表
            let active = this.$route.params.active;
            // 下载中
            if (active === 'downloading') {
                this.downloadList = this.downloadingList;
            }
            // 下载完成
            if (active === 'finished') {
                this.downloadList = this.finishedList;
            }
            // 垃圾箱
            if (active === 'trash') {
                this.downloadList = this.trashList;
            }
        },
        data() {
            return {
                downloadList: null,
            }
        },
        computed: {
            ...mapState([
                "downloadingList",
                "finishedList",
                "trashList",
            ])
        },
        components: {
            DownloadItem,
            RightBottom
        }
    }
</script>

